import React from 'react';
import './App.css';
import Header from './components/Header';
import Tasks from './components/Tasks';
import AddTask from './components/AddTask';
import { useState } from 'react'

  const name = "Fauci"
  const x = true
 
function App() {
  const [showAddTask, setShowAddTask] = useState (false)
  const [tasks, setTasks] = useState([
    {
        id: 1,
        text: 'Nationwide Public Mask Mandate',
        day: 'August 16th at 2.30pm CT',
        reminder: true,
    },
    {
        id: 2,
        text: 'Nationwide School Mask Mandate',
        day: 'August 16th at 2.30pm CT',
        reminder: true,
    },
    {
        id: 3,
        text: 'Nationwide Restaurant Mask Mandate',
        day: 'August 16th at 2.30pm CT',
        reminder: true,
    },
])

// Add Task
const addTask = (task) => {
  const id = Math.floor(Math.random() * 10000) + 1
  const newTask = { id, ...task }
  setTasks([...tasks, newTask])
}

// Delete Task
const deleteTask = (id) => {
  setTasks(tasks.filter((task) => task.id !==id))
}

// Toggle Reminder
const toggleReminder = (id) => {
  setTasks(
    tasks.map((task) => 
    task.id === id ? { ...task, reminder: !task.reminder } : task))
}

  return (
    <div className="container">
      <Header onAdd={() => setShowAddTask(!showAddTask)} showAdd={showAddTask} />
      {showAddTask && <AddTask onAdd={addTask} />}

      <h4>Emergency Response by {name}</h4>
      <h4>Masking Order: {1 + 1} Orders Active in the U.S.</h4>
      <h4>Delta Variant Active?: {x ? 'Yes' : 'No'}</h4>
      <br></br>
      {tasks.length > 0 ? <Tasks tasks={tasks} onDelete={deleteTask} onToggle={toggleReminder} /> : 'All CV-19 Tasks Completed!'}
      
    </div>
  );
}

/*
class App extends React.Component {
  render() {
    return <Header />
  }
}
*/

export default App;
